
var api = require('../api');
var view = require('../view');
var message = require('../message');

var template = "<h2>Login</h2>\n\n<form data-bind=\"submit: login\">\n    <div data-bind=\"errors: errors.username\" class=\"form-group\">\n        <label class=\"control-label\" for=\"username\">Username</label>\n        <input data-bind=\"textInput: username\"\n            type=\"text\" name=\"username\" title=\"Username\"\n            placeholder=\"Username\" class=\"form-control\" id=\"username\">\n    </div>\n    <div data-bind=\"errors: errors.password\" class=\"form-group\">\n        <label class=\"control-label\" for=\"password\">Password</label>\n        <input data-bind=\"textInput: password\"\n            type=\"password\" name=\"password\" title=\"Password\"\n            placeholder=\"Password\" class=\"form-control\" id=\"password\">\n    </div>\n    <div class=\"checkbox\">\n        <label><input data-bind=\"checked: remember\" type=\"checkbox\"\n            name=\"remember\" id=\"remember\">\n            Remember me</label>\n    </div>\n    <button type=\"submit\" class=\"btn btn-default\">\n        <span class=\"glyphicon glyphicon-log-in\"></span> Login</button>\n</form>\n";

// The login page.
exports.create = function() {

    var model = {
        username: ko.observable(''),
        password: ko.observable(''),
        remember: ko.observable(false),
        errors: {
            username: ko.observableArray([]),
            password: ko.observableArray([])
        }
    };

    model.login = function(form) {
        // Clear errors.
        Object.keys(model.errors).forEach(function(key) {
            model.errors[key]([]);
        });
        var username = model.username();
        var password = model.password();
        if (username === '') {
            model.errors.username.push('Username is not entered.');
        } else if (!username.match(/^[^@]+@[^@]+$/)) {
            model.errors.username.push('Username must be an email.');
        }
        if (password === '') {
            model.errors.password.push('Password is not entered.');
        }
        var input = form.querySelector(
            '.has-error input, .has-error textarea, .has-error checkbox');
        if (input) {
            input.focus();
            return false;
        }
        api.login(username, password).then(function(res) {
            if (model.remember()) {
                localStorage.setItem('api-key', res.key);
                sessionStorage.removeItem('api-key');
            } else {
                sessionStorage.setItem('api-key', res.key);
                localStorage.removeItem('api-key');
            }
            route.go('landing');
        }).catch(function(err) {
            if (err.toString().match(/Invalid auth credentials/)) {
                model.errors.username.push('Invalid auth credentials.');
                model.errors.password.push('Invalid auth credentials.');
            } else {
                message.error(err);
            }
        });
    };

    view.show(template, model);
    return Promise.resolve();
};
