
var api = require('../api');
var view = require('../view');
var message = require('../message');
var resolveObject = require('../resolve_object');

var template = "<h2>Configuration</h2>\n<!-- ko if: !permission() -->\n    <div class=\"alert alert-warning\" role=\"alert\">\n        You do not have permissions to edit configuration.\n    </div>\n<!-- /ko -->\n<!-- ko if: permission() -->\n    <!-- ko with: config -->\n        <div class=\"row\">\n            <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                <strong>Site base URL</strong></div>\n            <div data-bind=\"text: site\"\n                class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n        </div>\n        <div class=\"row\">\n            <div class=\"col-md-2 col-sm-3 col-xs-4 text-right\">\n                <strong>Title</strong></div>\n            <div data-bind=\"text: title\"\n                class=\"col-md-4 col-sm-5 col-xs-8\"></div>\n        </div>\n    <!-- /ko -->\n    <div class=\"row bc-margin-top\">\n        <div class=\"col-md-4 col-sm-5 col-xs-8 col-md-offset-2 col-sm-offset-3 col-xs-offset-4\">\n            <a href=\"#\" data-bind=\"click: edit\" class=\"btn btn-default\">\n                <span class=\"glyphicon glyphicon-pencil\"></span> Edit</a>\n        </div>\n    </div>\n    <!-- ko if: editing() -->\n        <hr>\n        <form data-bind=\"submit: save\" autocomplete=\"off\" id=\"config-form\">\n            <div data-bind=\"errors: errors.site\" class=\"form-group\">\n                <label for=\"config-site\">Site base URL</label>\n                <input data-bind=\"textInput: form.site\"\n                    type=\"text\" class=\"form-control\" id=\"config-site\"\n                    placeholder=\"Site base URL\">\n            </div>\n            <div class=\"form-group\">\n                <label for=\"config-title\">Title</label>\n                <input data-bind=\"textInput: form.title\"\n                    type=\"text\" class=\"form-control\" id=\"config-title\"\n                    placeholder=\"Site title\">\n            </div>\n            <button type=\"submit\" class=\"btn btn-default\">\n                <span class=\"glyphicon glyphicon-ok\"></span> Save\n            </button>\n            <a data-bind=\"click: cancel\" class=\"btn btn-default\" href=\"#\">\n                <span class=\"glyphicon glyphicon-remove\"></span> Cancel</a>\n        </form>\n    <!-- /ko -->\n<!-- /ko -->\n";

// Page for editing email settings.
exports.create = function(id) {

    var model = {
        permission: ko.observable(false),
        editing: ko.observable(false),
        config: ko.observable({}),
        form: {
            site: ko.observable(''),
            title: ko.observable('')
        },
        errors: {
            site: ko.observableArray([]),
            title: ko.observableArray([])
        }
    };

    // Saves settings and updates the
    // read-only views of options.
    model.save = function(form) {
        clearErrors();
        validateParams();
        // Checks whether some input
        // has error set.
        if (!checkError(form)) {
            return false;
        }
        var config = toConfig(model.form);
        api.updateConfig(toList(config)).then(function() {
            model.config(config);
            message.info('Configuration updated.');
            route.refresh();
        }).catch(message.error);
    };

    model.edit = function() {
        model.editing(true);
    };

    // Cancels editing. Refreshes current page.
    model.cancel = function() {
        route.refresh();
    };

    // Clears current form errors.
    function clearErrors() {
        Object.keys(model.errors).forEach(function(key) {
            model.errors[key]([]);
        });
    }

    // Validates parameters.
    function validateParams() {
        var form = model.form;
        if (form.site() === '') {
            model.errors.site.push('Site base URL must be set.');
        } else {
            if (!form.site().match(/^https?:\/\//)) {
                model.errors.site.push('Site base URL must start with http:// or https://.');
            }
            if (form.site().match(/\/$/)) {
                model.errors.site.push('Site base URL must not end with /.');
            }
        }
    }

    // Checks for errors on the form
    // elements. Focuses on the first
    // input with an error.
    function checkError(form) {
        var input = form.querySelector(
            '.has-error input, .has-error textarea,' +
            ' .has-error checkbox, .has-error select');
        if (input) {
            input.focus();
            return false;
        }
        return true;
    }

    return api.userInfo().then(function(info) {
        if (info.type !== 'admin') {
            view.show(template, model);
        } else {
            return api.config().then(function(data) {
                var config = extract(data);
                model.config(config);
                toForm(config, model.form);
                model.permission(true);
                view.show(template, model);
            });
        }
    });
};

// Extracts supported config settings from
// generic config entries.
function extract(config) {
    var obj = {};
    config.forEach(function(entry) {
        obj[entry.name] = entry.value;
    });
    return {
        site: obj.site,
        title: obj.title
    };
}

// Sets form values based on
// config values.
function toForm(config, form) {
    Object.keys(config).forEach(function(key) {
        form[key](config[key]);
    });
}

// Creates new object with
// values from the form binding
// object.
function toConfig(form) {
    var config = {};
    Object.keys(form).forEach(function(key) {
        config[key] = form[key]();
    });
    return config;
}

// Turns the configuration
// object into a list of configuration
// entries.

function toList(config) {
    return [
        { name: 'site', value: config.site },
        { name: 'title', value: config.title }
    ];
}
