
var api = require('../api');
var view = require('../view');
var resolveObject = require('../resolve_object');
var comments_item = require('../vm/comments_item');

var template = "<h2 data-bind=\"text: title\"></h2>\n<h3>Comments</h3>\n<ul data-bind=\"foreach: comments\" class=\"list-group\">\n    <li data-bind=\"style: { 'padding-left': (15 * (depth + 1)) + 'px' }\"\n        class=\"list-group-item post-item\">\n        <h4 data-bind=\"text: author\" class=\"list-group-item-heading\"></h4>\n        <div class=\"list-group-item-text\">\n            Date: <span data-bind=\"text: formatDate(date)\"></span>\n            <div class=\"post-controls\">\n                <div class=\"panel panel-default\">\n                    <div data-bind=\"text: content\" class=\"panel-body\"></div>\n                </div>\n                <!-- ko if: removable -->\n                    <a data-bind=\"click: remove\" class=\"btn btn-default btn-sm\" href=\"#\">\n                        <span class=\"glyphicon glyphicon-remove\"></span> Remove\n                    </a>\n                <!-- /ko -->\n            </div>\n        </div>\n    </li>\n</ul>\n<!-- ko if: comments().length === 0 -->\n    <div class=\"alert alert-info\" role=\"alert\">\n        This entry has no comments.\n    </div>\n<!-- /ko -->\n";

// Page for the comments list.
exports.create = function(type, id) {

    var model = {
        title: ko.observable(),
        comments: ko.observable([])
    };

    var requests = {
        entryInfo: api.entryInfo(id),
        userInfo: api.userInfo(id),
        typeInfo: api.typeInfo(type),
        comments: api.comments(id)
    };

    function flatten(comments) {
        var flat = [];
        flattenRec(comments, flat, 0);
        return flat;
    }

    function flattenRec(comments, list, depth) {
        comments.forEach(function(comment) {
            comment.depth = depth;
            list.push(comment);
            flattenRec(comment.replies, list, depth + 1);
        });
    }
    
    return resolveObject(requests).then(function(data) {
        model.title(data.entryInfo.title);
        model.comments(flatten(data.comments).map(function(comment) {
            return comments_item.create(comment,
                data.userInfo, data.entryInfo, data.typeInfo);
        }));
        view.show(template, model);
    });
};
