
var api = require('../api');
var view = require('../view');
var message = require('../message');
var trash_item = require('../vm/trash_item');

var template = "<h2>Trash</h2>\n<!-- ko if: items().length === 0 -->\n    <div class=\"alert alert-info\" role=\"alert\">\n        Trash has no entries.\n    </div>\n<!-- /ko -->\n<ul data-bind=\"foreach: items\" class=\"list-group\">\n    <li class=\"list-group-item post-item\">\n        <h4 data-bind=\"click: expand, text: title\" class=\"list-group-item-heading\"></h4>\n        <div class=\"list-group-item-text\">\n            Type: <span data-bind=\"text: type\"></span>\n            <div data-bind=\"visible: expanded\" class=\"post-controls\">\n                <a data-bind=\"click: remove\" class=\"btn btn-default btn-sm\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-remove\"></span> Remove\n                </a>\n                <a data-bind=\"click: restore\" class=\"btn btn-default btn-sm\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-floppy-disk\"></span> Restore\n                </a>\n            </div>\n        </div>\n    </li>\n</ul>\n<a data-bind=\"click: purge\" class=\"btn btn-default\" href=\"#\">\n    <span class=\"glyphicon glyphicon-remove\"></span> Purge trash\n</a>\n";

// Page for displaying the trash list.
exports.create = function() {

    var model = {
        items: ko.observableArray([])
    };

    model.purge = function() {
        if (confirm('Purge trash?')) {
            api.purge().then(function() {
                message.info('Trash is purged.');
                route.refresh();
            }).catch(message.error);
        }
    };
    
    return api.trash().then(function(items) {
        model.items(items.map(function(data) {
            return trash_item.create(data);
        }));
        view.show(template, model);
    });
};
