
var api = require('../api');
var view = require('../view');
var postsItem = require('../vm/posts_item');
var resolveObject = require('../resolve_object');

var template = "<h2 data-bind=\"text: title\"></h2>\n\n<div class=\"bc-margin-bottom\">\n    <div class=\"row\">\n        <div class=\"col-md-2\">\n            <div class=\"form-group\">\n                <label for=\"filter-tag\">Tag filter</label>\n                <select data-bind=\"value: tag, options: tags,\n                    optionsText: 'label', optionsValue: 'tag',\n                    optionsCaption: 'All tags'\"\n                    class=\"form-control input-sm\"></select>\n            </div>\n        </div>\n        <div class=\"col-md-2\">\n            <div class=\"form-group\">\n                <label for=\"filter-status\">Status filter</label>\n                <select data-bind=\"value: status\"\n                    class=\"form-control input-sm\">\n                    <option value=\"all\">All</option>\n                    <option value=\"published\">Published</option>\n                    <option value=\"unpublished\">Not published</option>\n                </select>\n            </div>\n        </div>\n    </div>\n</div>\n\n<ul data-bind=\"foreach: posts\" class=\"list-group\">\n    <li class=\"list-group-item post-item\">\n        <h4 data-bind=\"click: expand, text: title\" class=\"list-group-item-heading\"></h4>\n        <div class=\"list-group-item-text\">\n            Update date: <span data-bind=\"text: formatDate(date_updated)\"></span>\n            <!-- ko if: $parent.comments() -->\n                comments: <span data-bind=\"text: comments\"></span><br>\n            <!-- /ko -->\n            <!-- ko if: tags.length > 0 -->\n                <span data-bind=\"text: tags.join(', ')\" class=\"bc-entries-tag\"></span>\n            <!-- /ko -->\n            <div data-bind=\"visible: expanded\" class=\"post-controls bc-margin-bottom-sm\">\n                <!-- ko if: editable -->\n                    <a data-bind=\"attr: { href: editLink }\" class=\"btn btn-default btn-sm\" href=\"#\">\n                        <span class=\"glyphicon glyphicon-edit\"></span> Edit\n                    </a>\n                <!-- /ko -->\n                <!-- ko if: $parent.comments() -->\n                    <a data-bind=\"attr: { href: commentsLink }, css: { disabled: comments === 0 }\"\n                        class=\"btn btn-default btn-sm\" href=\"#\">\n                        <span class=\"glyphicon glyphicon-comment\"></span> Comments\n                    </a>\n                <!-- /ko -->\n                <!-- ko if: removable -->\n                    <a data-bind=\"click: remove\"\n                        class=\"btn btn-default btn-sm\" href=\"#\">\n                        <span class=\"glyphicon glyphicon-remove\"></span> Remove\n                    </a>\n                <!-- /ko -->\n            </div>\n            <!-- ko if: !published -->\n                <div>\n                    <span class=\"label label-warning\">Not published</span>\n                </div>\n            <!-- /ko -->\n        </div>\n    </li>\n</ul>\n\n<!-- ko if: posts().length === 0 -->\n    <div class=\"alert alert-info\" role=\"alert\">\n        No entries to show.\n    </div>\n<!-- /ko -->\n\n<div class=\"bc-controls\">\n    <a data-bind=\"click: showMore, css: { disabled: !hasMore() }\" class=\"btn btn-default\" href=\"#\">\n        <span class=\"glyphicon glyphicon-list-alt\"></span> Show more\n    </a>\n    <a data-bind=\"click: showAll, css: { disabled: !hasMore() }\" class=\"btn btn-default\" href=\"#\">\n        <span class=\"glyphicon glyphicon-list-alt\"></span> Show all\n    </a>\n    <a data-bind=\"attr: { href: '#new/' + type }\" class=\"btn btn-default\" href=\"#\">\n        <span class=\"glyphicon glyphicon-plus\"></span> Add\n    </a>\n</div>\n";

// Page for displaying a post list.
exports.create = function(type) {

    var model = {
        type: type,
        all: ko.observable([]),
        count: ko.observable(10),
        step: ko.observable(10),
        comments: ko.observable(false),
        create: ko.observable(false),
        title: ko.observable(),
        loaded: ko.observable(false),
        tags: ko.observable([]),
        tag: ko.observable(),
        status: ko.observable('all')
    };

    // List of filtered posts.
    model.filtered = ko.pureComputed(function() {
        var tag = model.tag();
        var status = model.status();
        var filtered = [];
        model.all().forEach(function(entry) {
            var tagMatch = !tag || entry.tags.indexOf(tag) > -1;
            var statusMatch = status === 'all' || (
                status === 'published' && entry.published) || (
                status === 'unpublished' && !entry.published);
            if (tagMatch && statusMatch) {
                filtered.push(entry);
            }
        });
        return filtered;
    });

    // Shows whether there are more
    // pager pages.
    model.hasMore = ko.pureComputed(function() {
        return model.count() < model.filtered().length;
    });

    // Shows new pager page with posts.
    model.showMore = function() {
        model.count(model.count() + model.step());
        setTimeout(function() {
            // Scrolls to the bottom.
            window.scrollTo(0, document.body.scrollHeight);
        }, 50);
    };

    // Shows all posts.
    model.showAll = function() {
        model.count(model.filtered().length);
    };

    // Posts array considering the current
    // pager state.
    model.posts = ko.pureComputed(function() {
        var all = model.filtered();
        return all.slice(0, model.count());
    });

    // Finds data from API and updates
    // the view.
    var requests = {
        typeInfo: api.typeInfo(type),
        userInfo: api.userInfo(),
        posts: api.posts(type),
        tags: api.tags(type)
    };

    return resolveObject(requests).then(function(data) {
        model.title(data.typeInfo.menu_label);
        model.comments(data.typeInfo.comments);
        var create = false;
        // Finds if the user can create entries.
        if (data.userInfo.type === 'admin') {
            create = true;
        }
        if (data.typeInfo.grants.indexOf('create') >= 0) {
            create = true;
        }
        model.create(create);
        data.tags.forEach(function(tag) {
            tag.label = tag.tag + ' (' + tag.count + ')';
        });
        data.tags.sort(function(t1, t2) {
            return t1.tag === t2.tag ? 0 : (t1.tag < t2.tag ? -1 : 1);
        });
        model.tags(data.tags);
        data.posts.sort(function(post1, post2) {
            return post2.date_updated - post1.date_updated;
        });
        model.all(data.posts.map(function(postData) {
            return postsItem.create(postData, data.typeInfo, data.userInfo);

        }));
        view.show(template, model);
    });
};
